.movies{
    display: grid;
    grid-template-columns: repeat(2, minmax(400px, 1fr));
    grid-gap: 100px;
    padding: 50px;
    width: 80%;
    padding-top: 50px;
}

body{
    margin: 0;
}

.container{
    height: 100%;
    display: flex;
    justify-content: center;
    background-color: gray;
}

.loader{
    width: 100%;
    height: 100cm;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;
}

@media screen and (max-width: 1000px) {
    .movies{
        grid-template-columns: 1fr;
        width: 100%;
    }
}